import React, { useRef } from 'react';
import Simple from './Simple/Simple.jsx';

function App() {
  let txtAreaRef = useRef(null);

  let [show, setShow] = React.useState(false);
  let changeShow = () => {
    setFunc(txtAreaRef.current.value)
    setShow(!show);
  }

  let [func, setFunc] = React.useState('');

  return (
    <div>
      {
        show ? 
        <Simple func={func} /> :
        <div> 
          <textarea ref={txtAreaRef} />
          <button onClick={changeShow}>Show simple</button>
        </div>
      }
      
    </div>
  );
}

export default App;
