import React from 'react';
import data from './simpleData';
import style from './style.module.css';

export default function Simple(props) { // Warn! ninja-code!!!
  let c = React.useRef(null);
  let a = React.useRef(null);
  let b = React.useRef(null);
  let d = React.useRef(null);
  let rrr = React.useRef(null);
  let g = React.useRef(null);

  window.props = props;

  let [showC, setshowC] = React.useState(false);
  let [showA, setshowA] = React.useState(false);
  let [showB, setshowB] = React.useState(false);
  let [showD, setshowD] = React.useState(false);
  let [showRrr, setshowRrr] = React.useState(false);
  let [showG, setshowG] = React.useState(false);


  return (
    <div className={style.simple}>
      <div className={style.simple__menu}>
        <label>
          <input
            onClick={() => setshowC(c.current.checked)}
            ref={c}
            type="checkbox"
          /> A
        </label>
        <label>
          <input
            onClick={() => setshowA(a.current.checked)}
            ref={a}
            type="checkbox"
          /> B
        </label>
        <label>
          <input
            onClick={() => setshowB(b.current.checked)}
            ref={b}
            type="checkbox"
          /> C
        </label>
        <label>
          <input
            onClick={() => setshowD(d.current.checked)}
            ref={d}
            type="checkbox"
          /> G
        </label>
        <label>
          <input
            onClick={() => setshowRrr(rrr.current.checked)}
            ref={rrr}
            type="checkbox"
          /> Li
        </label>
        <label>
          <input
            onClick={() => setshowG(g.current.checked)}
            ref={g}
            type="checkbox"
          /> Add
        </label>
      </div>

      
      {showC && <div className={style.simple__c}>
        <div className={style.simple_title}>C({data.c.length})</div>
        <div>{data.c.map(el => <Block el={el} func={props.func} />)}</div>
      </div>}

      {showA && <div className={style.simple__a}>
        <div className={style.simple_title}>A({data.a.length})</div>
        <div>{data.a.map(el => <Block el={el} func={props.func} />)}</div>
      </div>}

      {showB && <div className={style.simple__b}>
        <div className={style.simple_title}>B({data.b.length})</div>
        <div>{data.b.map(el => <Block el={el} func={props.func} />)}</div>
      </div>}

      {showD && <div className={style.simple__d}>
        <div className={style.simple_title}>D({data.d.length})</div>
        <div>{data.d.map(el => <Block el={el} func={props.func} />)}</div>
      </div>}

      {showRrr && <div className={style.simple__rrr}>
        <div className={style.simple_title}>RRR({data.rrr.length})</div>
        <div>{data.rrr.map(el => <Block el={el} func={props.func} />)}</div>
      </div>}

      {showG && <div className={style.simple__g}>
        <div className={style.simple_title}>G({data.g.length})</div>
        <div>{data.g.map(el => <Block el={el} func={props.func} />)}</div>
      </div>}

      {!showC && !showA && !showB && !showD && !showRrr && !showG && <div>
        Choice variant in menu
        ({data.c.length + data.g.length + data.a.length + data.b.length + data.rrr.length + data.d.length})
      </div>}
    </div>
  );
}

function Block({ el, func }) {
  let [showAnswer, setShowAnswer] = React.useState(false);

  return (
    <div
      className={showAnswer ? style.simple__answer : style.simple__question}
      onClick={() => setShowAnswer(prev => !prev)}
    >
      {showAnswer ?
        // <div>{eval(func + `'${el.answer}');`)}</div> :
        // <div>{eval(func + `'${el.question}');`)}</div>
        <div>{getAll(func, el.answer)}</div> :
        <div>{getAll(func, el.question)}</div>
      }
    </div>
  )
}

function getAll(func, str) {
  debugger
  if (str[str.length - 1] === '\\') {
    str += '\\';
  }

  let main = func + `'${str}');`;
  let res;

  try {
    res = eval(main);
  } catch {
    debugger;
    console.log(res, main);
    res = 'catch error'
  }
  
  return res;
}